import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppService } from 'src/app/app.service';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { FacultadModel } from 'src/app/commons/models/gestion-academica/facultad.model';
import { IndicadorPublicacionesModel } from 'src/app/commons/models/gestion-academica/indicador-publicaciones.model';
import { PeriodoModel } from 'src/app/commons/models/gestion-academica/periodo.model';
import { TipoAcademicoModel } from 'src/app/commons/models/gestion-academica/tipo-academico.model';
import { FacultadService } from 'src/app/commons/services/facultad.service';
import { TipoAcademicoService } from 'src/app/commons/services/tipo-academico.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { UtilValidation } from 'src/app/commons/util/util.validation';
import { guardarIndicadorPublicaciones, paginacionIndicadorPublicaciones, resetIndicadorPublicaciones } from 'src/app/store/actions/indicador-publicaciones.actions';
import { getPeriodos, guardarPeriodo, paginationPeriodo, resetPeriodo } from 'src/app/store/actions/periodo.actions';
import { AppState } from 'src/app/store/app.reducer';

@Component({
  selector: 'app-config-periodo',
  templateUrl: './config-indicador-publicaciones.component.html',
  styles: [
  ]
})
export class ConfigIndicadorPublicaciones implements OnInit, OnDestroy {

  public formIndPublicaciones: FormGroup;
  public indicadorPublicacion: IndicadorPublicacionesModel = new IndicadorPublicacionesModel();
  private _subscriptionIndPublicaciones: Subscription;
  public listaIndPublicaciones: IndicadorPublicacionesModel[];
  public listaTipoAcademico: TipoAcademicoModel[];
  public listaFacultades: FacultadModel[];
  public loaded: boolean = false;
  public loading: boolean = false;
  public indicadorPublicacionJson: string;
  public edit: boolean = false;
  public auxiliar: string;
  @BlockUI() blockUI: NgBlockUI;

  //-----------
  public page: number = 1;
  public pageSize: number = 10;
  public totalElements: number;

  constructor(
    private appService: AppService,
    public toastr: ToastrService,
    public store: Store<AppState>,
    public alertService: UtilAlert,
    public tipoAcademicoService: TipoAcademicoService,
    public facultadService: FacultadService
  ) {
    this.appService.pageTitle = 'Mantenedor de Indicador de Publicaciones - Adjudicaciones';
  }

  ngOnInit(): void {
    this.obtenerTipoAcademico();
    this.obtenerFacultades();
    this._resolucionState();
    this.crearIndicadorPublicaciones()
    this.listarIndPublicaciones();
  }
  ngOnDestroy(): void {
    this._subscriptionIndPublicaciones.unsubscribe();
  }

  private _resolucionState() {
    this._subscriptionIndPublicaciones = this.store.select("indicadorPublicaciones").subscribe(
      (state) => {
        if (state.searchPagination != null) {
          this.page = state.searchPagination.page;
          this.pageSize = state.searchPagination.records;

        }
        if (state.loaded && state.paginacionIndicadorPublicaciones) {
          this.loaded = state.loaded
          this.listaIndPublicaciones = state.paginacionIndicadorPublicaciones.content;
          this.totalElements = state.paginacionIndicadorPublicaciones.totalElements;
          this.blockUI.stop();
        }
        if (state.indicadorPublicaciones && state.loaded) {
          this.edit ? this.alertService.successSwal(`Se ha editado correctamente el indicador`) : this.alertService.successSwal(`Se ha registrado correctamente el indicador`);
          this.store.dispatch(resetIndicadorPublicaciones());
          this.edit = false;
          this.loading = false;
          this.listarIndPublicaciones();
          this.blockUI.stop()
        }
      }
    );
  }

  crearIndicadorPublicaciones() {
    this.formIndPublicaciones = new FormGroup({
      facultad: new FormControl(null, [Validators.required]),
      modulosFormacion: new FormControl(null, [Validators.required, Validators.min(0)]),
      numeroPublicaciones: new FormControl(null, [Validators.required, Validators.min(0)]),
      adjudicacionesAnual: new FormControl(null, [Validators.min(0)]),
      tipoAcademico: new FormControl(null, [Validators.required]),
    });
  }

  listarIndPublicaciones() {
    this.listaIndPublicaciones = [];
    const searchPagination: SearchPagination<string> = {
      page: this.page,
      records: this.pageSize,
      seek: "",
    };
    this.blockUI.start("Cargando...");
    this.store.dispatch(paginacionIndicadorPublicaciones({ searchPagination })
    );
  }

  formularioReset() {
    if (this.edit) {
      this.formIndPublicaciones.get("facultad").enable();
      this.formIndPublicaciones.get("tipoAcademico").enable();
    }
    this.formIndPublicaciones.get("modulosFormacion").setValue(null);
    this.formIndPublicaciones.get("numeroPublicaciones").setValue(null);
    this.formIndPublicaciones.get("adjudicacionesAnual").setValue(null);
    this.formIndPublicaciones.get("modulosFormacion").markAsUntouched();
    this.formIndPublicaciones.get("numeroPublicaciones").markAsUntouched();
    this.formIndPublicaciones.get("adjudicacionesAnual").markAsUntouched();
  }

  obtenerTipoAcademico() {
    this.tipoAcademicoService.getAllTipoAcademico().subscribe((value) => {
      this.listaTipoAcademico = value
    }, (error) => {
      this.toastr.error("No se ha podido obtener la lista de los tipos de académicos")
    })
  }

  setEdicion(indPublicacion: IndicadorPublicacionesModel) {
    this.edit = true;
    this.indicadorPublicacion = indPublicacion;
    this.formIndPublicaciones.get("facultad").setValue(indPublicacion.facultadId.codigo);
    this.formIndPublicaciones.get("modulosFormacion").setValue(indPublicacion.modulosFormacion);
    this.formIndPublicaciones.get("numeroPublicaciones").setValue(indPublicacion.numeroPublicaciones);
    this.formIndPublicaciones.get("adjudicacionesAnual").setValue(indPublicacion.adjudicacionesAnual);
    this.formIndPublicaciones.get("tipoAcademico").setValue(indPublicacion.tipoAcademico.id);

    this.formIndPublicaciones.get("facultad").disable();
    this.formIndPublicaciones.get("tipoAcademico").disable();
  }

  obtenerFacultades() {
    this.facultadService.getAllFacultades().subscribe((value) => {
      this.listaFacultades = value
    }, (error) => {
      this.toastr.error("No se ha podido obtener la lista de las facultades")
    })
  }

  obtenerDatosFormulario() {
    debugger;
    if (this.edit) {
      let indicadorPublicacionesObtenido = new IndicadorPublicacionesModel;
      indicadorPublicacionesObtenido.facultadId = this.indicadorPublicacion.facultadId;
      indicadorPublicacionesObtenido.tipoAcademico = this.indicadorPublicacion.tipoAcademico;
      indicadorPublicacionesObtenido.id = this.indicadorPublicacion.id;

      indicadorPublicacionesObtenido.modulosFormacion = this.formIndPublicaciones.get("modulosFormacion").value
      indicadorPublicacionesObtenido.numeroPublicaciones = this.formIndPublicaciones.get("numeroPublicaciones").value
      if (this.formIndPublicaciones.get("adjudicacionesAnual").value == "") {
        indicadorPublicacionesObtenido.adjudicacionesAnual = null;
      } else {
        indicadorPublicacionesObtenido.adjudicacionesAnual = this.formIndPublicaciones.get("adjudicacionesAnual").value
      }
      this.indicadorPublicacion = new IndicadorPublicacionesModel();
      this.indicadorPublicacion = indicadorPublicacionesObtenido;
    } else {
      this.indicadorPublicacion = new IndicadorPublicacionesModel()
      this.indicadorPublicacion.adjudicacionesAnual = this.formIndPublicaciones.get("adjudicacionesAnual").value
      let facultad = new FacultadModel();
      facultad.codigo = this.formIndPublicaciones.get("facultad").value
      this.indicadorPublicacion.facultadId = facultad
      this.indicadorPublicacion.modulosFormacion = this.formIndPublicaciones.get("modulosFormacion").value
      this.indicadorPublicacion.numeroPublicaciones = this.formIndPublicaciones.get("numeroPublicaciones").value
      let tipoAcademico = new TipoAcademicoModel();
      tipoAcademico.id = this.formIndPublicaciones.get("tipoAcademico").value
      this.indicadorPublicacion.tipoAcademico = tipoAcademico
    }
    this.formularioReset();
  }

  guardar() {
    UtilValidation.setFormForValidate(this.formIndPublicaciones);
    if (this.formIndPublicaciones.invalid) {
      this.toastr.info("Debe ingresar todos los datos antes de guardar");
      return;
    }
    this.obtenerDatosFormulario();
    this.blockUI.start("Guardando");
    this.loading = true;
    this.store.dispatch(guardarIndicadorPublicaciones({ indicadorPublicaciones: this.indicadorPublicacion }))
  }
}
