import { FacultadModel } from './facultad.model';
import { TipoAcademicoModel } from './tipo-academico.model';

export class IndicadorPublicacionesModel {

    public id: number = 0;
    public facultadId: FacultadModel = null;
    public modulosFormacion: number = 0;
    public numeroPublicaciones: number = 0;
    public adjudicacionesAnual: number = 0;
    public tipoAcademico: TipoAcademicoModel = null;

    constructor(fields?: {
        id?: number,
        facultadId?: FacultadModel,
        modulosFormacion?: number,
        numeroPublicaciones?: number,
        adjudicacionesAnual: number,
        tipoAcademico?: TipoAcademicoModel,
    }) {
        if (fields) { Object.assign(this, fields); }

    }
}